<template>
  <div class="text-start mt-2">
    <v-data-table
      :loading="loading"
      :headers="headers"
      :items="dataTable"
      items-per-page-all-text
      class="elevation-1"
      hide-default-footer
    >
      <template v-slot:item.value="{ item }">
        <div v-if="typeof item.value == 'object'" > <!-- for value is objectType. ex: mqtt_rest_bridge_listener={} -->
          <span class="font-weight-medium mr-2" v-for="(key, i) of Object.keys(item.value)" :key="i">
            <span v-if="typeof item.value[key] == 'boolean'"> <!-- for the child value is boolean. ex: mqtt_rest_bridge_listener.enabled= true -->
              <v-chip v-if="item.value[key]" dark color="green" small>{{ toUpperTextKey(key) }}</v-chip>
              <v-chip v-else dark color="grey" small>{{ toUpperTextKey(key) }}</v-chip>
            </span>
            <span v-else-if="typeof item.value[key] == 'object'" > <!-- for sub_value is object. ex: mqtt_rest_bridge_listener.mqtt = {} -->
              <span class="font-weight-medium mr-2" v-for="(subkey, si) of Object.keys(item.value[key])" :key="si">
                <span v-if="typeof item.value[key][subkey] == 'boolean'"> <!-- for sub_value's value is boolean. ex: mqtt_rest_bridge_listener.mqtt.enabled= true -->
                  <v-chip v-if="item.value[key][subkey]" dark color="green" small class="mt-2">{{toUpperTextKey(key)}} : {{  toUpperTextKey(subkey) }}</v-chip>
                  <v-chip v-else dark color="grey" small class="mt-2">{{toUpperTextKey(key)}} : {{  toUpperTextKey(subkey) }}</v-chip>
                </span>
                <span v-else>{{item.value[key]}}</span>
              </span>
            </span>
            <span v-else>{{item.value[key]}}</span> <!-- for the child value is string. ex: mqtt_rest_bridge_listener.mqtt= 'aaaaaa' -->
          </span>
        </div>
        <div v-else>{{item.value}}</div>
      </template>
    </v-data-table>
  </div>
</template>

<script>
import axios from '../../axios';
import { toUpperTextKey } from '../../utils/utils';

export default {
  props: ['server'],
  data: () => ({
    toUpperTextKey: toUpperTextKey,
    headers: [
      { text: 'Name', value: 'name', width: '200px' },
      { text: 'Value', value: 'value', sortable: false },
    ],
    dataTable: [],
    loading: false,
  }),
  mounted() {
    this.getSystemStatus();
  },
  methods: {
    getUrlApi() {
      switch (this.server) {
        case 'modbus':
          return 'point-server';
        case 'bac-net':
          return 'bac-net';
        case 'lora':
          return 'lora';
        case 'bacnet-master':
          return 'bacnet_master';
        case 'rubix-service':
          return 'rubix-service';
      }
    },
    async getSystemStatus() {
      try {
        this.loading = true;
        const url = this.getUrlApi();
        const response = await axios.get(`${url}/system/ping`);
        this.dataTable = Object.keys(response).map(key => ({ name: toUpperTextKey(key), value: response[key] }));
      } catch (error) {
      }finally {
        this.loading = false;
       }
      
    },
  },
};
</script>
<style scoped>
.value {
  font-weight: bold;
  opacity: 0.6;
}
</style>
